import React, { useEffect } from "react";
import { compose } from "redux";
import { connect } from "react-redux";
import { fetchServices } from "../api/actions";
import { Paper, Divider } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";

const mapStateToProp = ({ services }) => ({ services });

const mapDispatchToProps = dispatch => {
  return {
    getServices: () => dispatch(fetchServices())
  };
};

const enhance = compose(
  connect(
    mapStateToProp,
    mapDispatchToProps
  )
);

const useStyles = makeStyles({
  paper: {
    padding: 15,
    margin: 10
  }
});

const Services = props => {
  const { getServices } = props;
  useEffect(() => {
    getServices();
  }, [getServices]);

  const classes = useStyles();

  const services = props.services.services
    ? props.services.services.map(service => {
        return (
          <Paper key={service._id} className={classes.paper}>
            <h2>{service.title}</h2>
            <Divider />
            {service.description}
          </Paper>
        );
      })
    : null;
  return (
    <div>
      <h2>Services</h2>
      {services}
    </div>
  );
};

export default enhance(Services);
